import logo from "assets/svg/logo.svg";
import { SearchBar } from "components/common";
import { CardGrid } from "components/home";

import "./Home.scss";

export default function Home() {
  return (
    <main className="luiza-heroes-home">
      <div className={"luiza-heroes-home__header container"}>
        <img src={logo} alt="logo" />
        <h1>Explore o Universo</h1>
        <p>
          Mergulhe no domnio deslumbrante de todos os personagens clssicos que
          voc ama - e aqueles que voc descobrir em breve
        </p>
        <SearchBar placeholder="Procure por heris"></SearchBar>
      </div>

      <CardGrid></CardGrid>
    </main>
  );
}
